<template>
  <ui-breadcrumb>
    <ui-breadcrumbItem
      v-for="(item,key) in currentPath"
      :href="item.path"
      :key="key"
    >{{item.title}}</ui-breadcrumbItem>
  </ui-breadcrumb>
</template>

<script>
  export default {
    name: 'breadcrumbNav',
    props: {
      currentPath: Array
    }
  };
</script>




