<template>
  <div id="main" class="main-layout" @keydown="shortCutKey" tabIndex="1">
    <div class="main-layout-header-bar">
      <div class="main-layout-header">
        <div class="navicon-con" @mouseenter="showMenu" @mouseleave="hideMenu">
          <ui-button type="text" style="margin-top:6px;">
            <ui-icon type="navicon" size="32"
                     :style="{transform: 'rotateZ(' + (this.toShowMenu ? '0' : '90') + 'deg)'}"></ui-icon>
            <span class="menu-title">菜单</span>
          </ui-button>
        </div>
        <div class="logo-con">
          <!--<i class="logo-src">金证财富</i>-->
          <img src="../src/assets/img/logo.png" alt="金证股份">
        </div>
        <div class="header-middle-con">
          <div class="main-breadcrumb">
            <breadcrumb-nav :currentPath="currentPaths"></breadcrumb-nav>
          </div>
        </div>
        <div class="header-avator-con">
          <div @click="handleFullScreen" v-if="showFullScreenBtn" class="full-screen-btn-con">
            <ui-tooltip :content="isFullScreen ? '退出全屏' : '全屏'" placement="bottom">
              <ui-icon :type="isFullScreen ? 'arrow-shrink' : 'arrow-expand'" :size="23"></ui-icon>
            </ui-tooltip>
          </div>
          <div @click="lockScreen" class="lock-screen-btn-con" v-show="showLockSwitch">
            <ui-tooltip content="锁屏" placement="bottom">
              <ui-icon type="locked" :size="20"></ui-icon>
            </ui-tooltip>
          </div>
          <div @click="showMessage" class="message-con">
            <ui-tooltip :content="messageCount > 0 ? '有' + messageCount + '条未读消息' : '无未读消息'" placement="bottom">
              <ui-badge :count="messageCount" dot>
                <ui-icon type="ios-bell" :size="22"></ui-icon>
              </ui-badge>
            </ui-tooltip>
          </div>
          <div class="switch-theme-con" v-show="showChangeTheme">
            <ui-row class="switch-theme" type="flex" justify="center" align="middle">
              <theme-dropdown-menu></theme-dropdown-menu>
            </ui-row>
          </div>
          <div @click="handleLogout" class="logout-con">
            <ui-tooltip content="注销" placement="bottom">
              <ui-icon type="log-out" :size="18"></ui-icon>
            </ui-tooltip>
          </div>
          <div class="avatar-con">
            <span
                style="font: 12px/1.5 Tahoma,Helvetica,Arial,'宋体',sans-serif;margin-left:10px;margin-right:2px;">{{`${userName}[${userId}]`}}</span>
            <ui-avatar :src="avatorPath" style="background: #619fe7;"></ui-avatar>
          </div>
        </div>
      </div>
      <div class="tags-con">
        <tags-page-opened :pageTagsList="pageTagsList"></tags-page-opened>
      </div>
    </div>
    <div class="main-layout-menu" v-show="toShowMenu" @mouseenter="showMenu" @mouseleave="hideMenu">
      <cascade-menu :isRootMenu="isRootMenu" :removeClass="removeClass" :menuList="menuList"
                    @on-show-submenu="showSubMenu"
                    @on-show-grandmenu="showGrandMenu" @on-select="routerPage"></cascade-menu>
      <cascade-menu :isSubMenu="isSubMenu" :subMenuShow="subMenuShow" :menuList="subMenuList"
                    @on-show-grandmenu="showGrandMenu"
                    @on-show-forthmenu="showForthMenu" @on-select="routerPage"></cascade-menu>
      <cascade-menu :isGrandMenu="isGrandMenu" :grandMenuShow="grandMenuShow" :menuList="grandMenuList"
                    @on-show-forthmenu="showForthMenu" @on-select="routerPage"></cascade-menu>
      <cascade-menu :isForthMenu="isForthMenu" :forthMenuShow="forthMenuShow" :menuList="forthMenuList"
                    @on-select="routerPage"></cascade-menu>
    </div>
    <div class="single-page">
      <ui-spin fix v-show="showLoading" style="background-color:rgba(255, 255, 255, 0.2)">
        <ui-icon type="load-c" size=26  class="frame-spin-icon-load"></ui-icon>
        <div>请求正在处理中，请稍后...</div>
      </ui-spin>
      <div class="frame-layout-router">
        <keep-alive :exclude="excludeRouter">
          <router-view></router-view>
        </keep-alive>
      </div>
    </div>
    <div class="short-cut-box" v-show="showShortCutBox">
      <ui-icon type="close" size="11" color="#FFFF" style="position:absolute;right:13px;cursor:pointer"
               @click.native="closeShortCut"></ui-icon>
      <div style="color:#FFFF;padding:2px;">
        <span>请输入关键字：</span>
      </div>
      <div class="short-cut-box-prepend">
        <ui-select style="width:70px;display:inline-block" :value=searchContent>
          <ui-option value="menu">菜单</ui-option>
          <!--<ui-option value="other">其他</ui-option>-->
        </ui-select>
      </div>
      <div style="display:inline-block;width:307px;">
        <ui-auto-complete placeholder="快捷键[Ctrl+M]" :clearable=true ref="shortCutInput" v-model="shortCutMenu"
                          :data=shortCutMenuList @on-search="handleSearch"
                          @on-select="shortCutRouterPage"></ui-auto-complete>
      </div>
    </div>
  </div>
</template>

<script>
  import cascadeMenu from './Frame/cascadeMenu';
  import tagsPageOpened from './Frame/tagsPageopened.vue';
  import breadcrumbNav from './Frame/breadcrumbNav.vue';
  import themeDropdownMenu from './Frame/themeDropdownMenu.vue';
  import util from './Frame/util.js';
  import Cookies from 'js-cookie';

  export default {
    components: {
      cascadeMenu,
      tagsPageOpened,
      breadcrumbNav,
      themeDropdownMenu
    },
    data () {
      return {
        toShowMenu: false,
        isRootMenu: true,
        isSubMenu: true,
        isGrandMenu: true,
        isForthMenu: true,
        subMenuShow: false,
        grandMenuShow: false,
        forthMenuShow: false,
        subMenuList: [],
        grandMenuList: [],
        forthMenuList: [],
        removeClass: true,
        isFullScreen: false,
        messageCount: 3,
        showFullScreenBtn: window.navigator.userAgent.indexOf('MSIE') < 0,
        showLockSwitch: false,
        showChangeTheme: false,
        showShortCutBox: false,
        searchContent: 'menu',
        shortCutMenuList: [],
        shortCutMenu: '',
        menuPathMap: {},
        currentPaths: []
      };
    },
    computed: {
      menuList () {
        return this.$store.getters.navMenu;
      },
      pageTagsList () {
        return this.$store.getters.pageOpenedList;  // 打开的页面的页面对象
      },
      tagsList () {
        return this.$store.getters.tagsList;
      },
      avatorPath () {
        return localStorage.avatorImgPath;
      },
      userName () {
        return this.$store.getters.login_user_name;
      },
      userId () {
        return this.$store.getters.login_user_id;
      },
      shortCutMenus () {
        let menus = [];
        let activeRoutes = this.$store.getters.activeRoutes;
        let routes = this.$router.options.routes.concat(activeRoutes);
        for (let i = 0; i < routes.length; i++) {
          if (routes[i].children) {
            for (let j = 0; j < routes[i].children.length; j++) {
              let moduleMenus = routes[i].children[j];
              menus.push(moduleMenus.name);
              this.menuPathMap[moduleMenus.name] = moduleMenus.path;
            }
          }
        }
        return menus;
      },
      excludeRouter () {
        return this.$store.getters.excludePages;
      },
      showLoading () {
        return this.$store.getters.showLoading;
      }
    },
    methods: {
      showMenu () {
        this.toShowMenu = true;
      },
      hideMenu () {
        this.toShowMenu = false;
        this.subMenuShow = false;
        this.grandMenuShow = false;
        this.forthMenuShow = false;
        this.removeClass = this.removeClass !== true;
      },
      showSubMenu (data) {
        // 根据状态来决定是否显示子菜单
               
        this.subMenuShow = data.status;
        if (!this.subMenuShow) return this;
        for (let i = 0; i < this.menuList.length; i++) {
          let group = this.menuList[i]._sub;
          let match = false;
          for (let j = 0; j < group.length; j++) {
            if (group[j].menuId === data.menuid) {
              this.subMenuList = [group[j]];
              match = true;
              break;
            }
          }
          if (match) break;
        }
      },
      showGrandMenu (data) {
        // 根据状态来决定是否显示子菜单
        this.grandMenuShow = data.status;
        if (!this.grandMenuShow) return this;
        let grandMenus = this.subMenuList[0]._sub;
        for (let i = 0; i < grandMenus.length; i++) {
          if (grandMenus[i].menuId === data.menuid) {
            this.grandMenuList = [grandMenus[i]];
            break;
          }
        }
      },
      showForthMenu (data) {
        // 根据状态来决定是否显示子菜单
        this.forthMenuShow = data.status;
        if (!this.forthMenuShow) return this;
        let forthMenus = this.grandMenuList[0]._sub;
        for (let i = 0; i < forthMenus.length; i++) {
          if (forthMenus[i].menuId === data.menuid) {
            this.forthMenuList = [forthMenus[i]];
            break;
          }
        }
      },
      routerPage (path) {
        
        let pageOpenedList = this.$store.getters.pageOpenedList;
        let openedPageLen = pageOpenedList.length;
        let i = 0;
        let tagHasOpened = false;
        while (i < openedPageLen) {
          if (path === pageOpenedList[i].path) {  // 页面已经打开
            if (path !== '/home') {
              this.$store.commit('MOVE_TO_SECOND', i);
            }
            tagHasOpened = true;
            break;
          }
          i++;
        }
        if (!tagHasOpened) {
          let tag = [];
          for (let element of this.tagsList) {
            if (element.children) {
              tag = element.children.filter((item) => {
                if (item.children) {
                  return path === item.children[0].path;
                } else {
                  return path === item.path;
                }
              });
            }
            if (tag && tag.length !== 0) break;
          }
          tag = tag[0];
          if (!tag) return;
          tag = tag.children ? tag.children[0] : tag;
          // debugger
          this.$store.commit('INCREATE_TAG', tag);
          this.$store.commit('REMOVE_EXCLUDE_PAGE', tag.path);
          localStorage.pageOpenedList = JSON.stringify(this.$store.getters.pageOpenedList); // 本地存储已打开页面
        }
        this.$router.push(path);
        this.toShowMenu = false;
      },
      handleFullScreen () {
        let main = document.body;
        if (this.isFullScreen) {
          if (document.exitFullscreen) {
            document.exitFullscreen();
          } else if (document.mozCancelFullScreen) {
            document.mozCancelFullScreen();
          } else if (document.webkitCancelFullScreen) {
            document.webkitCancelFullScreen();
          } else if (document.msExitFullscreen) {
            document.msExitFullscreen();
          }
        } else {
          if (main.requestFullscreen) {
            main.requestFullscreen();
          } else if (main.mozRequestFullScreen) {
            main.mozRequestFullScreen();
          } else if (main.webkitRequestFullScreen) {
            main.webkitRequestFullScreen();
          } else if (main.msRequestFullscreen) {
            main.msRequestFullscreen();
          }
        }
      },
      lockScreen () {
        let lockScreenBack = document.getElementById('lock_screen_back');
        lockScreenBack.style.transition = 'all 3s';
        lockScreenBack.style.zIndex = 10000;
        lockScreenBack.style.boxShadow = '0 0 0 ' + this.lockScreenSize + 'px #667aa6 inset';
        this.showUnlock = true;
        this.$store.commit('LOCK_SCREEN');
        Cookies.set('last_page_name', this.$route.name); // 本地存储锁屏之前打开的页面以便解锁后打开
        setTimeout(() => {
          lockScreenBack.style.transition = 'all 0s';
          this.$router.push('/locking');
        }, 800);
      },
      showMessage () {
        util.openPage(this, '/message', '消息中心');
      },
      handleLogout () {
        this.$uiModal.confirm({
          title: '用户注销',
          content: '确定要注销当前登录吗？',
          onOk: () => {
            this.$store.dispatch('logout');
            window.location.reload();
            this.$store.commit('CLEAR_OPEND_TAG', this.$router);
            Cookies.remove('user');
          }
        });
      },
      shortCutKey (eve) {
        let keyCode = eve.keyCode || eve.which || eve.charCode;
        let ctrlKey = eve.ctrlKey || eve.metaKey;
        // ctrl+m 快捷键实现路由跳转
        if (ctrlKey && keyCode === 77) {
          this.shortCutMenu = '';
          this.showShortCutBox = true;
          window.setTimeout(function () {
            fn();
          }, 0);
        }
        if (keyCode === 27) {
          this.closeShortCut();
        }
        let that = this;

        function fn () {
          that.$refs.shortCutInput.$children[0].$refs.reference.children[0].children[1].focus();
        }
      },
      handleSearch (value) {
        if (this.searchContent === 'menu') {
          let filterMenu = [];
          let menus = this.shortCutMenus;
          for (let i = 0; i < menus.length; i++) {
            if (menus[i].indexOf(value) !== -1) {
              filterMenu.push(menus[i]);
            }
          }
          this.shortCutMenuList = filterMenu;
        }
      },
      closeShortCut () {
        this.showShortCutBox = false;
        this.$el.focus();
      },
      shortCutRouterPage (value) {
        this.routerPage(this.menuPathMap[value]);
        if (value) {
          this.closeShortCut();
        }
      }
    },
    beforeCreate () {
      this.$store.dispatch('getMenu');
    },
    created () {
      // 显示打开的页面的列表
      this.$store.commit('SET_OPEND_LIST', this.$router);
      this.$store.commit('CLEAR_OPEND_TAG', this.$router);
    },
    mounted () {
      this.currentPaths = util.setCurrentPath(this, this.$route.name);
      this.isFullScreen = document.fullscreenElement || document.mozFullScreenElement ||
        document.webkitFullscreenElement || document.fullScreen || document.mozFullScreen ||
        document.webkitIsFullScreen;
      this.isFullScreen = typeof this.isFullScreen !== 'boolean';
      let that = this;
      // 全屏相关
      document.addEventListener('fullscreenchange', () => {
        fn(that);
      });
      document.addEventListener('mozfullscreenchange', () => {
        fn(that);
      });
      document.addEventListener('webkitfullscreenchange', () => {
        fn(that);
      }, false);
      document.addEventListener('msfullscreenchange', () => {
        fn(that);
      });

      function fn (that) {
        that.isFullScreen = !that.isFullScreen;
      }

      // 锁屏相关
      let lockScreenBack = document.getElementById('lock_screen_back');
      let x = document.body.clientWidth;
      let y = document.body.clientHeight;
      let r = Math.sqrt(x * x + y * y);
      let size = parseInt(r);
      this.lockScreenSize = size;
      window.addEventListener('resize', () => {
        let x = document.body.clientWidth;
        let y = document.body.clientHeight;
        let r = Math.sqrt(x * x + y * y);
        let size = parseInt(r);
        this.lockScreenSize = size;
        lockScreenBack.style.transition = 'all 0s';
        lockScreenBack.style.width = lockScreenBack.style.height = size + 'px';
      });
      lockScreenBack.style.width = lockScreenBack.style.height = size + 'px';
      // 获取焦点，以便监听快捷键的按键事件
      this.$el.focus();
    },
    watch: {
      '$route' (to) {
        this.currentPaths = util.setCurrentPath(this, to.name);
  }
    }
  };
</script>



