<template>
  <ui-dropdown trigger="click" @on-click="setTheme">
    <a href="javascript:void(0)">
      <ui-icon :style="{marginTop: '-2px', verticalAlign: 'middle'}" color="#495060" :size="18" type="paintbucket"></ui-icon>
      <ui-icon type="arrow-down-b"></ui-icon>
    </a>
    <ui-dropdown-menu slot="list">
      <ui-dropdown-item v-for="(item, index) in themeList" :key="index" :name="item.name">
        <ui-row type="flex" justify="center" align="middle">
          <span style="margin-right:10px;"><ui-icon :size="20" :type="item.name.substr(0, 1) !== 'b' ? 'happy-outline' : 'happy'" :color="item.menu"></ui-icon></span>
          <span><ui-icon :size="22" type="record" :color="item.element"></ui-icon></span>
        </ui-row>
      </ui-dropdown-item>
    </ui-dropdown-menu>
  </ui-dropdown>
</template>

<script>
  import Cookies from 'js-cookie';
  export default {
    name: 'themeDropdownMenu',
    data () {
      return {
        themeList: [
          {
            name: 'black_b',
            menu: '#495060',
            element: '#2d8cf0'
          },
          {
            name: 'black_g',
            menu: '#495060',
            element: '#64d572'
          },
          {
            name: 'black_y',
            menu: '#495060',
            element: '#ffd572'
          },
          {
            name: 'black_r',
            menu: '#495060',
            element: '#f25e43'
          },
          {
            name: 'light_b',
            menu: '#495060',
            element: '#2d8cf0'
          },
          {
            name: 'light_g',
            menu: '#495060',
            element: '#64d572'
          },
          {
            name: 'light_y',
            menu: '#495060',
            element: '#ffd572'
          },
          {
            name: 'light_r',
            menu: '#495060',
            element: '#f25e43'
          }
        ]
      };
    },
    methods: {
      setTheme (themeFile) {
        let menuTheme = themeFile.substr(0, 1);
        let mainTheme = themeFile.substr(-1, 1);
        if (menuTheme === 'b') {
          // 黑色菜单
          this.$store.commit('STORAGE_THEME', 'dark');
          menuTheme = 'dark';
        } else {
          this.$store.commit('STORAGE_THEME', 'light');
          menuTheme = 'light';
        }
        let path = '';
        let themeLink = document.querySelector('link[name="theme"]');
        let userName = Cookies.get('user');
        if (localStorage.theme) {
          let themeList = JSON.parse(localStorage.theme);
          let index = 0;
          let hasThisUser = themeList.some((item, i)=> {
            if (item.userName === userName) {
              index = i;
              return true;
            } else {
              return false;
            }
          });
          if (hasThisUser) {
            themeList[index].mainTheme = mainTheme;
            themeList[index].menuTheme = menuTheme;
          } else {
            themeList.push({
              userName: userName,
              mainTheme: mainTheme,
              menuTheme: menuTheme
            });
          }
          localStorage.theme = JSON.stringify(themeList);
        } else {
          localStorage.theme = JSON.stringify([{
            userName: userName,
            mainTheme: mainTheme,
            menuTheme: menuTheme
          }]);
        }
        if (mainTheme !== 'b') {
          path = '/dist/' + mainTheme + '.css';
        } else {
          path = '';
        }
        themeLink.setAttribute('href', path);
      }
    }
  };
</script>



