<template>
  <div style="display:inline-block;float:left;">
    <div :style="menuClasses" v-show="toShow">
      <div class="cascade-menu">
        <div v-for="(item,key) in menuList" :key="key">
          <h2 class="menu-title">{{item.description}}</h2>
          <ul>
            <li v-for="(menuItem,key1) in item._sub" :key="key1"
                :class="['menu-line',{'actived': menuId==menuItem.menuId},{'menu-arrow':menuItem._sub!=undefined}]"
                @mouseenter="enterEvent(menuItem)" @mouseout="outEvent(menuItem)" @click.stop="handleClick">
              {{menuItem.description}}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'cascadeMenu',
    data () {
      return {
        menuId: ''
      };
    },
    methods: {
      enterEvent (menuItem) {
        this.menuId = menuItem.menuId;
        // 如果当前光标在第一级目录，则显示第二级；同时，若有第三级目录，则隐藏第三级
        if (this.isRootMenu) {
          this.$emit('on-show-submenu', {menuid: this.menuId, status: true});
          this.$emit('on-show-grandmenu', {status: false});
        }
        if (this.isSubMenu) {
          // 如果当前菜单没有子菜单则隐藏第三级目录，否则显示第三级目录
          if (menuItem._sub !== undefined) {
            this.$emit('on-show-grandmenu', {menuid: this.menuId, status: true});
          } else {
            this.$emit('on-show-grandmenu', {menuid: this.menuId, status: false});
          }
          // 若有第四级目录，则隐藏第四级
          this.$emit('on-show-forthmenu', {status: false});
        }
        if (this.isGrandMenu) {
          // 如果当前菜单没有子菜单则隐藏第四级目录，否则显示第四级目录
          if (menuItem._sub !== undefined) {
            this.$emit('on-show-forthmenu', {menuid: this.menuId, status: true});
          } else {
            this.$emit('on-show-forthmenu', {menuid: this.menuId, status: false});
          }
        }
      },
      outEvent (menuItem) {
        if (menuItem._sub === undefined) {
          this.menuId = '';
        }
      },
      handleClick (event) {
        let classList = event.currentTarget.classList;
        
        if (classList.length === 3 || (classList.value && classList.value.indexOf('menu-arrow') !== -1)) {
          return false;
        } else {
          this.$emit('on-select', '/' + this.menuId);
        }
      }
    },
    computed: {
      menuClasses () {
        return {
          height: `${document.body.clientHeight - 60}px`,
          overflow: 'auto',
          width: '240px'
        };
      },
      toShow () {
        this.menuId = '';
        return this.isRootMenu || this.subMenuShow || this.grandMenuShow || this.forthMenuShow;
      }
    },
    props: {
      menuList: {
        type: Array,
        default: []
      },
      isRootMenu: {
        type: Boolean,
        default: false
      },
      isSubMenu: {
        type: Boolean,
        default: false
      },
      isGrandMenu: {
        type: Boolean,
        default: false
      },
      isForthMenu: {
        type: Boolean,
        default: false
      },
      subMenuShow: {
        type: Boolean,
        default: false
      },
      forthMenuShow: {
        type: Boolean,
        default: false
      },
      grandMenuShow: {
        type: Boolean,
        default: false
      },
      removeClass: {
        type: Boolean
      }
    },
    watch: {
      removeClass () {
        this.menuId = '';
      }
    }
  };
</script>



