<template>
  <div style="position:absolute;margin-top:3px;">
    <transition-group name="taglist-moving-animation">
      <ui-tag
          type="dot"
          v-for="item in pageTagsList"
          :key="item.path"
          @on-close="closePage(item.path)"
          @contextmenu.native.prevent="closeTagMenu"
          @click.native="linkTo(item.path)"
          :closable="item.path==='/home'?false:true"
          :color="item.children?(item.children[0].path===currentPagePath?'blue':'default'):(item.path===currentPagePath?'blue':'default')"
      >{{ item.title }}
      </ui-tag>
    </transition-group>
    <div :style=rightMenuClass v-show="showRightMenu">
      <div class="close-tab" @click="closeOtherTags"><span class="font-style">关闭其他标签</span></div>
      <div class="close-tab" @click="closeAllTags" style="border-bottom:0px"><span
          class="font-style">关闭所有标签</span></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'tagsPageOpened',
    data () {
      return {
        currentPagePath: this.$route.path,
        currPageX: 0,
        currPageY: 0,
        showRightMenu: false,
        clickTagPath: ''
      };
    },
    props: {
      pageTagsList: Array
    },
    computed: {
      title () {
        return this.$store.state.currentTitle;
      },
      rightMenuClass () {
        return {
          'border-radius': '4px',
          'box-shadow': '0 1px 6px rgba(0,0,0,.2)',
          'white-space': 'nowrap',
          'width': '100px',
          'height': '50px',
          'background-color': '#fff',
          'background-clip': 'padding-box',
          'position': 'absolute',
          'z-index': 99,
          'top': `${this.currPageY - 60}px`,
          'left': `${this.currPageX}px`
        };
      }
    },
    methods: {
      closePage (path) {
        this.$store.commit('REMOVE_TAG', path);
        localStorage.pageOpenedList = JSON.stringify(this.$store.getters.pageOpenedList);
        if (this.currentPagePath === path) {
          let lastPagePath = '';
          let pageOpened = this.$store.getters.pageOpenedList;
          if (pageOpened.length > 1) {
            lastPagePath = pageOpened[1].path;
          } else {
            lastPagePath = pageOpened[0].path;
          }
          this.$router.push(lastPagePath);
        }
        this.addExcludePage(path, 'self');
      },
      linkTo (path) {
        this.$router.push(path);
      },
      closeTagMenu (event) {
        this.currPageX = event.pageX;
        this.currPageY = event.pageY;
        let openedPages = this.$store.getters.pageOpenedList;
        if (openedPages.length > 1) {
          this.showRightMenu = true;
          let pageName = event.currentTarget.innerText.trim();
          let path = openedPages.filter((item) => item.name === pageName);
          this.clickTagPath = path[0].path;
        }
      },
      closeOtherTags () {
        this.addExcludePage(this.clickTagPath, 'other');
        this.$store.commit('REMOVE_OTHER_TAG', this.clickTagPath);
        localStorage.pageOpenedList = JSON.stringify(this.$store.getters.pageOpenedList);
        this.$router.push(this.clickTagPath);
      },
      closeAllTags () {
        this.addExcludePage(this.clickTagPath, 'all');
        this.$store.commit('REMOVE_ALL_TAG');
        localStorage.pageOpenedList = JSON.stringify(this.$store.getters.pageOpenedList);
        this.$router.push('/home');
      },
      // 加后缀防止死循环
      addExcludePage (path, flag) {
        let pageNames = this.$store.getters.pageExportNames;
        let name = pageNames.get(path) + '-page';
        let param = {name: name, flag: flag};
        this.$store.commit('ADD_EXCLUDE_PAGE', param);
      }
    },
    watch: {
      '$route' (to) {
        this.currentPagePath = to.path;
      }
    },
    created () {
      document.addEventListener('click', () => {
        this.showRightMenu = false;
      }, true);
      document.addEventListener('contextmenu', () => {
        this.showRightMenu = false;
      }, true);
    }
  };
</script>
<style>
  .close-tab {
    padding: 2px 0px 2px 15px;
    border-bottom: 1px solid;
    border-bottom-color: #9ea7b4;
    cursor: pointer;
  }

  .close-tab:hover {
    background-color: #d5f0e3;
  }

  .font-style {
    font-size: 10px;
  }
</style>



