let util = {};

util.inOf = function (arr, targetArr) {
  let res = true;
  arr.map((item) => {
    if (targetArr.indexOf(item) < 0) {
      res = false;
    }
  });
  return res;
};

util.oneOf = function (ele, targetArr) {
  if (targetArr.indexOf(ele) >= 0) {
    return true;
  } else {
    return false;
  }
};

util.showThisRoute = function (itAccess, currentAccess) {
  if (typeof itAccess === 'object' && itAccess.isArray()) {
    return util.oneOf(currentAccess, itAccess);
  } else {
    return itAccess === currentAccess;
  }
};

util.getPathObjByName = function (vm, name) {
  let pathObj = vm.$store.state.routers.filter((item) => {
    if (item.children.length <= 1) {
      return item.name === name;
    } else {
      let i = 0;
      let childArr = item.children;
      let len = childArr.length;
      while (i < len) {
        if (childArr[i].name === name) {
          return true;
        }
        i++;
      }
      return false;
    }
  })[0];
  return pathObj;
};

/**
 * 用面包屑组建展示路由跳转的路径
 * @param vm 当前vue实例
 * @param name 页面名称
 * @returns {[*]}
 */
util.setCurrentPath = function (vm, name) {
  let currentPathArr = [
    {
      title: '首页',
      path: '/home',
      name: '首页'
    }
  ];
  if (name !== '首页') {
    let breadPathMap = vm.$store.getters.breadPaths;
    let breadPaths = breadPathMap.get(name);
    if (breadPaths) {
      breadPaths.forEach((item) => {
        currentPathArr.push({
          title: item,
          name: item
        });
      });
    }
  }
  // 面包屑组件显示当前页面路径
  vm.$store.commit('SET_CURRENT_PAGE', currentPathArr);
  return currentPathArr;
};

/**
 * @param vm vue实例 必填
 * @param routePath 路由匹配的路径 必填
 * @param title 页面标题 必填
 * @param filePath 文件路径 跳临时页面时必填
 * @param params 自定义参数
 */
util.openPage = function (vm, routePath, title, filePath, params) {
  let tempRoutes = vm.$store.getters.tempRoutes;
  let hasRoute = tempRoutes[0] ? tempRoutes[0].children.some((item) => item.path === routePath) : false;
  if (!hasRoute) {
    vm.$store.commit('ADD_TEMP_ROUTE', {
      routePath: routePath,
      name: title,
      title: title,
      filePath: filePath
    });
  }
  vm.$router.push({
    path: routePath,
    query: params
  });
  let hasOpened = false;
  vm.$store.getters.pageOpenedList.forEach((item, index) => {
    if (item.path === routePath) {
      hasOpened = true;
      vm.$store.commit('MOVE_TO_SECOND', index);
    }
  });
  if (!hasOpened) {
    vm.$store.commit('INCREATE_TAG', {path: routePath, name: title, title: title});
    vm.$store.commit('REMOVE_EXCLUDE_PAGE', routePath);
  }
};

export default util;



